.Card-bg{
    width: 30rem;
    height: 30rem;
    background-color: #eeeeee;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-top: 50px;
}

.Card-Header{
    background-color: #444444;
    height: 15%;
    width: 100%;
    color: #ffffff;
    display: flex;
    align-items: center;
    justify-content: center;
}
.Card-Body{
    
    height: 80%;
    width: 100%;
}
.Card-Footer{
    background-color: #dddddd;
    height: 10%;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}